export default {
  data () {
    return {
      isShow: true,
      hasAnimate: true
    }
  },
  computed: {
    /**
     * 动画效果样式，没有返回空
     * @return {String} 样式
     */
    animateName () {
      return this.hasAnimate ? 'opacity' : ''
    }
  },
  methods: {
    /**
     * 开启动画效果
     */
    opemAnimate () {
      this.hasAnimate = true
    },
    /**
     * 去除动画效果
     * @return {Promise} 返回promise
     */
    removeAnimate () {
      return new Promise((resolve) => {
        this.hasAnimate = false
        resolve()
      })
    },
    /**
     * 显示动画loading
     */
    show () {
      this.isShow = true
    },
    /**
     * 隐藏动画loading
     */
    hide () {
      this.isShow = false
    }
  }
}



